import NavBar from "./NavBar";
import Footer from "./Footer";
import "../../css/Onas.css"
const Onas = () => {
    return (
        <>
        <NavBar />
        <div className="onas">
            <h1>O nas</h1>
            <p>Dziaamy z myl o tych, ktrzy chc robi zakupy atwo, szybko i wygodnie. Kady, znajdzie to, co lubi w przystpnej cenie. Tym, co nas wyrnia, jest ekspresowa wysyka nawet w cigu 15 minut od zoenia zamwienia.</p>
        </div>
        <Footer />
        </>
    )
}

export default Onas