import NavBar from "./NavBar";
import Footer from "./Footer";
import textImg from "../../images/text.webp";
import phoneImg from "../../images/phone.png";
import "../../css/Kontakt.css"

const Kontakt = () => {
    return (
        <>
        <NavBar />
        <div className="kontakt">
            <h2>Kontakt</h2>
            <p className="description">Jeli masz pytania lub problem z zamwieniem, zapraszamy do kontaktu z naszym Dziaem Obsugi Klienta.</p>
            <div className="contacts">
                <div className="contact-email">
                    <img src={textImg}></img>
                    <span>Napisz do nas na czacie online</span>
                    <span>poniedziaekpitek: 7:0021:00</span>
                    <span>sobotaniedziela: 8:0016:00</span>
                </div>
                <div className="contact-phone">
                    <img className="phone" src={phoneImg}></img>
                    <p>504 323 213</p>
                </div>
            </div>
        </div>
        <Footer />
        </>
    )
}

export default Kontakt