import { useState } from "react"
import "../../css/Popup.css"

const PopUp = () => {
    const [showPopUp, setShowPopUp] = useState("popup");
    const closePopUp = () => {
        setShowPopUp('popup-none');
    }  
      return(
        <div className={showPopUp}>
            <p>Korzystamy z plikw cookies lub podobnych technologii w celu zapewnienia Ci najlepszych dowiadcze z korzystania z naszej strony internetowej. Zgadzajc si na wszystkie pliki cookies pozwolisz nam na zadbanie o Twj komfort podczas dokonywania zakupw na podstawie Twoich wasnych preferencji, nawykw oraz dopasowania wywietlania naszej oferty indywidualnie do Twoich potrzeb.</p>
            <button className="x" onClick={closePopUp}>X</button>
            <button onClick={closePopUp}>Akceptuj</button>
        </div>
    )
}

export default PopUp